import React, { useContext } from 'react';

import { Context } from '../context';

const PreMint = ({ nextStep, details }) => {
  const [context] = useContext(Context);

  const { amountDAI, stepDetails } = context;

  const { visualComponents, commonVisualProps } = stepDetails.preMint;

  const { visual: Pre, props: defaultProps } = visualComponents.default;

  const onSubmit = () => {
    nextStep();
  };

  return (
    <Pre
      amountDAI={amountDAI}
      totalAmount={amountDAI}
      treeFrequency="1 Tree Per Month"
      onSubmit={onSubmit}
      {...defaultProps}
      {...commonVisualProps}
    />
  );
};

export default PreMint;
