import React from "react"
import StepWizard from "react-step-wizard"

import WizardContext from "./WizardContext"

import InitialStep from "./InitialStep"
import LoadWallet from "./Wallet/LoadWallet"
import UnlockWallet from "./Wallet/UnlockWallet"
import ChooseWallet from "./Wallet/ChooseWallet"
import Swap0x from "./Wallet/Swap0x"
import PreApprove from "./PreApprove"
import FireTx from "./FireTx/FireTx"
import PreMint from "./PreMint"
import FinalStep from "./FinalStep"

const debug = false

const getInfoStep = (step, name) => {
  if (name === "preApprove") return <PreApprove key="pre-approve" />
  if (name === "preMint") return <PreMint key="pre-mint" />
  if (name === "success") return <FinalStep key="success" details={step} />
  return <InitialStep key="initial" />
}

const getWeb3Step = step => {
  const { type } = step
  if (type === "choose-wallet") return <ChooseWallet key="choose-wallet" />
  if (type === "unlock-wallet") return <UnlockWallet key="unlock-wallet" />
  if (type === "load-wallet") return <LoadWallet key="load-wallet" />
  if (type === "swap-0x") return <Swap0x key="swap-0x" />
  if (type === "transaction") return <FireTx key="web3-tx" stepDetails={step} />
}

const Wizard = ({ options, onToggleActive = () => {}, infuraKey }) => {
  const { stepDetails, id } = options
  const { stepOrder = Object.keys(stepDetails) } = options
  const steps = stepOrder.map(name => {
    if (typeof stepDetails[name] === "undefined")
      return (
        <>{`Invalid Step name: "${name}". Must match a step listed in stepDetails: ${Object.keys(
          stepDetails
        )}`}</>
      )
    const { category } = stepDetails[name]
    if (category === "info") return getInfoStep(stepDetails[name], name)
    if (category === "web3") return getWeb3Step(stepDetails[name])
    return null
  })
  return (
    <>
      <WizardContext
        stepOrder={stepOrder}
        stepDetails={stepDetails}
        id={id}
        infuraKey={infuraKey}
      />
      <StepWizard
        initialStep={1}
        isLazyMount
        isHashEnabled={debug}
        onStepChange={stats => {
          if (stats.activeStep === 1 || stats.previousStep === 1)
            onToggleActive(id)
        }}
      >
        {steps}
      </StepWizard>
    </>
  )
}

export default Wizard
