import React, { useContext } from "react"
import { Context } from "../context"

const InitialStep = ({ nextStep }) => {
  const [context, setContext] = useContext(Context)
  const { stepDetails, componentId } = context

  if (stepDetails && stepDetails.initial) {
    const { visualComponents, commonVisualProps } = stepDetails.initial
    const { visual: Initial, props: initialProps } = visualComponents.initial
    const onSubmit = amount => {
      setContext({
        ...context,
        amountDAI: amount,
      })
      nextStep()
    }
    return (
      <Initial
        {...commonVisualProps}
        {...initialProps}
        onSubmit={onSubmit}
        id={componentId}
      />
    )
  }
  return <></>
}

export default InitialStep
