import WalletLink from 'walletlink';
const ethers = require('ethers');
const axios = require('axios');

const CONTRACTS = require('./constants');
// require("babel-polyfill")

const { parseUnits, formatUnits, bigNumberify } = ethers.utils;

class Web3Utils {
  async getWalletlessProvider({ network, infuraKey }) {
    try {
      const walletlessProvider = new ethers.providers.InfuraProvider(
        network,
        infuraKey
      );
      return { walletlessProvider };
    } catch (error) {
      return this.getErrorResponse(error, 'getWalletlessProvider');
    }
  }

  async unlockWallet() {
    if (!this.isWeb3EnabledBrowser()) {
      return { hasWallet: false, isUnlocked: false };
    }
    let walletAddress = '';
    try {
      window.ethereum.autoRefreshOnNetworkChange = false;
      walletAddress = await window.ethereum.enable();
      localStorage.setItem('walletAddress', walletAddress[0]);

      // console.log(window.ethereum.autoRefreshOnNetworkChange)
      const walletProvider = new ethers.providers.Web3Provider(
        window.web3.currentProvider
      );
      const network = await walletProvider.getNetwork();
      return {
        hasWallet: true,
        isUnlocked: true,
        walletAddress: walletAddress[0],
        network,
        walletProvider
      };
    } catch (error) {
      if (typeof window.ethereum === 'undefined') {
        return { hasWallet: false, isUnlocked: false };
      }
      // eslint-disable-next-line no-console
      return {
        hasWallet: true,
        isUnlocked: false,
        error: this.getErrorResponse(error, 'unlockWallet')
      };
    }
  }

  async get0xOrder({ buyToken, sellToken, buyAmount }) {
    try {
      const params = `?buyToken=${buyToken}&sellToken=${sellToken}&buyAmount=${buyAmount}`;
      const res = await axios.get(`https://api.0x.org/swap/v0/quote${params}`);
      console.log(res.data);
      return { order: res.data };
    } catch (error) {
      return {
        error: this.getErrorResponse(error, 'get0xOrder')
      };
    }
  }

  async send0xOrder({ order, walletProvider }) {
    try {
      const { value, to, data } = order;
      const tx = {
        to: order.to,
        value: bigNumberify(order.value),
        data: order.data
      };
      const signer = walletProvider.getSigner();
      const res = await signer.sendTransaction(tx);
      return {
        tx: res
      };
    } catch (error) {
      return {
        error: this.getErrorResponse(error, 'send0xOrder')
      };
    }
  }

  async unlockCoinbaseWallet(infuraKey) {
    if (!this.isWeb3EnabledBrowser()) {
      return { hasWallet: false, isUnlocked: false };
    }
    let walletAddress = '';
    try {
      const APP_NAME = 'dappy';
      const APP_LOGO_URL = 'https://dappy.dev/favicon.ico';
      const ETH_JSONRPC_URL = `https://mainnet.infura.io/v3/${infuraKey}`;
      const CHAIN_ID = 1;
      const walletLink = new WalletLink({
        appName: APP_NAME,
        appLogoUrl: APP_LOGO_URL
      });
      const ethereum = walletLink.makeWeb3Provider(ETH_JSONRPC_URL, CHAIN_ID);
      const web3 = new Web3(ethereum);
      const accounts = await ethereum.send('eth_requestAccounts');
      const walletProvider = new ethers.providers.Web3Provider(
        web3.currentProvider
      );
      const network = await walletProvider.getNetwork();
      return {
        hasWallet: true,
        isUnlocked: true,
        walletAddress: accounts[0],
        network,
        walletProvider
      };
      // Optionally, have the default account be set automatically when available
      // ethereum.on('accountsChanged', (accounts: string[]) => {
      //   web3.eth.defaultAccount = accounts[0];
      // });
      // web3.eth.defaultAccount = web3.eth.accounts[0];
    } catch (error) {
      return {
        isUnlocked: false,
        error: this.getErrorResponse(error, 'unlockWallet')
      };
    }
  }

  async loadContracts({
    walletProvider,
    contractNames,
    allowancesRequested,
    balancesRequested,
    isWatchOnly
  }) {
    try {
      ethers.errors.setLogLevel('error'); // "debug", "default", "info", "warn", "off"
      const loadedContracts = {};
      const { name: networkName } = await walletProvider.getNetwork();

      const providerOrSigner = isWatchOnly
        ? walletProvider
        : walletProvider.getSigner();

      contractNames.forEach(name => {
        loadedContracts[name] = new ethers.Contract(
          CONTRACTS[name][networkName],
          CONTRACTS[name].abi,
          providerOrSigner
        );
      });

      const owner = await walletProvider
        .listAccounts()
        .then(accounts => accounts[0]);

      let allowancesObj;
      if (allowancesRequested) {
        const allowances = await this.getAllowances({
          contracts: loadedContracts,
          owner,
          allowancesRequested
        });
        allowancesObj = allowances;
      }

      const { balances } = await this.getBalances({
        contracts: loadedContracts,
        balancesRequested,
        owner
      });
      // eslint-disable-next-line no-console
      console.log(
        `Loaded contracts ${Object.keys(loadedContracts)} on ${networkName}`
      );
      return {
        contracts: loadedContracts,
        allowances: allowancesObj,
        balances
      };
    } catch (error) {
      return this.getErrorResponse(error, 'loadContracts');
    }
  }

  async approve(tokenContract, owner, spender, amount, allowUnlimitedApprove) {
    try {
      // TODO: get decimal value, rather than hardcoded 18
      let amount_BN = parseUnits(amount.toString(), 18);
      // TODO: add an argument to make optional isApprovalNeeded()
      const approvalNeeded = await this.isApprovalNeeded(
        tokenContract,
        owner,
        spender,
        amount_BN
      );
      let gasPrice = await tokenContract.provider.getGasPrice();
      const overrides = {
        gasPrice: gasPrice.toNumber() * 1.5
      };

      if (approvalNeeded.error) {
        throw approvalNeeded.error;
      } else if (approvalNeeded) {
        if (allowUnlimitedApprove) amount_BN = parseUnits('500000', 18);
        const tx = await tokenContract.approve(spender, amount_BN, overrides);
        return {
          tx
        };
      }
      return {
        approvalComplete: true
      };
    } catch (error) {
      return this.getErrorResponse(error, 'approve');
    }
  }

  async isApprovalNeeded(tokenContract, owner, spender, amount_BN) {
    try {
      // Todo, perform check if BN and convert if Needed
      // Remove the BN conversion from Step 3
      // console.log(owner, spender)
      const allowance_BN = await tokenContract.allowance(owner, spender);
      console.log('Allowance: ', formatUnits(allowance_BN, 18));
      console.log('Approval needed: ', amount_BN.gt(allowance_BN));
      return amount_BN.gt(allowance_BN);
    } catch (error) {
      return this.getErrorResponse(error, 'isApprovalNeeded');
    }
  }

  // Currently unused
  async mintRDAI({ rtokenContract, amount }) {
    try {
      const amount_BN = parseUnits(amount.toString(), 18);
      const tx = await rtokenContract.mint(amount_BN);
      return {
        tx
      };
    } catch (error) {
      return this.getErrorResponse(error, 'generateRDAI');
    }
  }

  // Default mint method used in rTrees
  // Adds additional gas to the tx, otherwise success rate is only ~50% for rDAI mint.
  async mintWithSelectedHatRDAI({ rtokenContract, amount, hatID }) {
    try {
      // https://github.com/ethers-io/ethers.js/issues/469#issuecomment-475926538
      const amount_BN = parseUnits(amount.toString(), 18);
      const overrides = {
        gasLimit: 1330000 // 1.33 M gas
      };
      const tx = await rtokenContract.mintWithSelectedHat(
        amount_BN,
        hatID,
        overrides
      );
      return {
        tx
      };
    } catch (error) {
      return this.getErrorResponse(error, 'mintWithSelectedHatRDAI');
    }
  }

  async redeemAllRDAI({ rtokenContract }) {
    const overrides = {
      gasLimit: 700000 // 1.33 M gas
    };
    try {
      const tx = await rtokenContract.redeemAll(overrides);
      return {
        tx
      };
    } catch (error) {
      return this.getErrorResponse(error, 'redeemAllRDAI');
    }
  }

  async getAllowances({ contracts, owner, allowancesRequested }) {
    try {
      const tokenContracts = allowancesRequested.tokens.map(
        tokenName => {
          return contracts[tokenName];
        }
        // TODO add error handling for contract/allowance mismatch
      );

      const spenderAddressList = allowancesRequested.spenders.map(
        spenderName => {
          if (
            spenderName.search(/0x[0-9a-bA-z]{40}/) > 0 ||
            spenderName.search(/.eth/) > 0
          ) {
            return spenderName; // return ENS or raw address
          }
          return contracts[spenderName].address;
        }
      );
      const allowances = {};
      // console.log(tokenContracts)
      for (let i = 0; i < tokenContracts.length; i++) {
        const contract = tokenContracts[i];
        const name = allowancesRequested.tokens[i];
        for (let j = 0; j < spenderAddressList.length; j++) {
          const spender = spenderAddressList[i];
          let allowance;
          try {
            allowance = await contract.allowance(owner, spender);
          } catch (error) {
            allowance = {
              error: `Error getting allowance: ${error.message}`
            };
          }
          const allowanceObj = {
            [allowancesRequested.spenders[j]]: allowance
          };
          allowances[name] = allowanceObj;
        }
      }

      return allowances;
    } catch (error) {
      console.log(error);
      return this.getErrorResponse(error, 'getAllowances');
    }
  }

  async getBalances({ contracts, owner, balancesRequested }) {
    try {
      const balances = {};
      if (balancesRequested && balancesRequested.length > 0) {
        for (let i = 0; i < balancesRequested.length; i++) {
          const tokenName = balancesRequested[i];
          const contract = contracts[tokenName];
          balances[tokenName] = await contract.balanceOf(owner);
        }
      }
      return { balances };
    } catch (error) {
      console.log(error);
      return this.getErrorResponse(error, 'getBalances');
    }
  }

  checkRequiredBalance({ requiredBalances, balances }) {
    try {
      const insufficientBalances = {};

      if (requiredBalances) {
        for (let i = 0; i < Object.keys(requiredBalances).length; i++) {
          const tokenName = Object.keys(requiredBalances)[i];
          const balance = balances[tokenName];
          const requiredBalance = parseUnits(
            requiredBalances[tokenName].toString(),
            18
          );
          if (balance.lt(requiredBalance)) {
            insufficientBalances[tokenName] = {
              balance,
              requiredBalance,
              difference: requiredBalance.sub(balance)
            };
          }
        }
      }
      return insufficientBalances;
    } catch (error) {
      console.log(error);
      return this.getErrorResponse(error, 'checkRequiredBalance');
    }
  }

  async getCompoundRate() {
    try {
      const res = await axios.get(
        'https://api.compound.finance/api/v2/ctoken?addresses[]=0x5d3a536e4d6dbd6114cc1ead35777bab948e3643'
      );
      const rate = res.data.cToken[0].supply_rate.value;
      return { rate, formattedRate: Math.round(rate * 10000) / 100 };
    } catch (error) {
      return this.getErrorResponse(error, 'getCompoundRate');
    }
  }

  // ////////////////////
  // General Functions

  isWeb3EnabledBrowser() {
    return (
      typeof window !== 'undefined' && typeof window.ethereum !== 'undefined'
    );
  }

  isBrowser() {
    return typeof window !== 'undefined';
  }

  getErrorResponse(error, functionName) {
    const res = {
      message: `Error web3Utils.${functionName}(): ${error.message}`
    };
    if (error.code) {
      res.code = error.code;
      switch (error.code) {
        case 4001:
          res.txErrorType = 'aborted';
          break;
        case -32016:
          res.txErrorType = 'exception';
          break;
        default:
          res.txErrorType = 'unknown error type';
      }
    }
    return { error: res };
  }
}

export default Web3Utils;
