import React, { useContext, useState, useEffect } from 'react';
import { Context } from '../../context';

const UnlockWallet = ({ nextStep, goToStep }) => {
  const [context, setContext] = useContext(Context);

  const initialState = {
    doUnlockWallet: true,
    walletStatus: null
  };
  const [state, setState] = useState(initialState);
  const { doUnlockWallet, walletStatus } = state;

  const { stepDetails, web3Utils } = context;

  const { visualComponents, commonVisualProps } = stepDetails.unlockWallet;

  const { visual: Unlock, props: unlockProps } = visualComponents.unlock;
  const { visual: Install, props: installProps } = visualComponents.install;
  const { visual: Network, props: networkProps } = visualComponents.network;
  const { visual: Error, props: errorProps } = visualComponents.error;

  const onRetry = () => {
    setState(initialState);
    goToStep(1);
  };

  let display;
  switch (walletStatus) {
    case 'error':
      display = (
        <Error onSubmit={onRetry} {...commonVisualProps} {...errorProps} />
      );
      break;
    case 'install':
      display = (
        <Install onSubmit={onRetry} {...installProps} {...commonVisualProps} />
      );
      break;
    case 'network':
      display = <Network {...networkProps} {...commonVisualProps} />;
      break;

    default:
      display = <Unlock {...unlockProps} {...commonVisualProps} />;
  }

  const unlockWallet = async () => {
    setState({
      ...state,
      doUnlockWallet: false
    });

    const {
      hasWallet,
      isUnlocked,
      walletAddress,
      walletProvider,
      network,
      error
    } = await web3Utils.unlockWallet();

    if (error) {
      console.log(error.error.message);
      setState({
        ...state,
        doUnlockWallet: false,
        walletStatus: 'error'
      });
      return;
    }

    setContext({
      ...context,
      hasWallet,
      isUnlocked,
      walletAddress,
      walletProvider,
      network,
      error: ''
    });

    if (!hasWallet) {
      setState({
        ...state,
        doUnlockWallet: false,
        walletStatus: 'install'
      });
      return;
    }

    const isCorrectNetwork =
      network && (network.name === 'kovan' || network.name === 'homestead');

    if (!isCorrectNetwork) {
      setState({
        ...state,
        doUnlockWallet: false,
        walletStatus: 'network'
      });
      return;
    }

    nextStep();
  };

  // TODO : fix memory leak for case "network","install". re-render on "error" as well

  useEffect(() => {
    if (web3Utils && doUnlockWallet) unlockWallet();
  }, [doUnlockWallet]);

  return <>{display}</>;
};

export default UnlockWallet;
