import React from 'react';
import PropTypes from 'prop-types';

const Swap0xView = ({
  buyToken,
  sellToken,
  buyAmount,
  sellAmount,
  fee,
  onPlaceOrder
}) => {
  return (
    <>
      <ul>
        <li>Sell Token: {buyToken}</li>
        <li>Sell Amount: {sellAmount}</li>
        <li>Buy Token: {sellToken}</li>
        <li>Buy Amount: {buyAmount}</li>
        <li>Fee: {fee} wei ETH</li>
      </ul>
      <button onClick={() => onPlaceOrder()}>Swap</button>
    </>
  );
};

Swap0xView.propTypes = {
  buyToken: PropTypes.string.isRequired,
  sellToken: PropTypes.string.isRequired,
  buyAmount: PropTypes.string.isRequired,
  sellAmount: PropTypes.string,
  fee: PropTypes.string,
  onPlaceOrder: PropTypes.func.isRequired
};
Swap0xView.defaultProps = {
  sellAmount: 'loading',
  fee: 'loading'
};

export default Swap0xView;
