import React, { useContext } from 'react';
import { Context } from '../context';

const FinalStep = ({ goToStep }) => {
  const [context] = useContext(Context);
  const { stepDetails } = context;
  const { visualComponents, commonVisualProps } = stepDetails.success;

  const { visual: Success, props: successProps } = visualComponents.default;

  // Pick out the action type, to allow using react-wizard goToStep().
  const { onSubmitActionType, onSubmit, ...restProps } = successProps;
  let _onSubmit = onSubmit;
  if (onSubmitActionType === 'restart') _onSubmit = () => goToStep(1);
  if (onSubmitActionType === 'refresh' && typeof location !== 'undefined')
    _onSubmit = () => location.reload();
  return <Success {...restProps} {...commonVisualProps} onSubmit={_onSubmit} />;
};

export default FinalStep;
