import React, { useContext, useState } from 'react';
// import { utils } from "ethers"
import { Context } from '../context';

const PreApprove = ({ nextStep }) => {
  const [context, setContext] = useContext(Context);
  const [state, setState] = useState({
    isChecked: true
  });

  const { stepDetails } = context;
  const { visualComponents, commonVisualProps } = stepDetails.preApprove;

  const { visual: Pre, props: preApproveProps } = visualComponents.preApprove;

  const onToggleCheck = () => {
    setState({ ...state, isChecked: !state.isChecked });
  };
  const onSubmit = () => {
    setContext({
      ...context,
      allowUnlimitedApprove: state.isChecked
    });
    nextStep();
  };

  return (
    <Pre
      onToggleCheck={onToggleCheck}
      onSubmit={onSubmit}
      isChecked={state.isChecked}
      {...preApproveProps}
      {...commonVisualProps}
    />
  );
};

export default PreApprove;
