import React from 'react';
import Wizard from './DappFlow/Wizard';
import { ContextProvider } from './context';

const Dappy = options => {
  try {
    if (typeof options === 'undefined' || options === null) return <>Error</>;
    return (
      <ContextProvider>
        <Wizard {...options} />
      </ContextProvider>
    );
  } catch (e) {
    console.log(e);
  }
};

export default Dappy;
