import React, { useContext, useState, useEffect } from "react"

import { Context } from "../../context"

const FireTx = ({ txType, nextStep, isActive, goToStep, stepDetails }) => {
  const [context] = useContext(Context)
  const initialState = {
    doFireTx: true,
    txStatus: null,
  }
  const [state, setState] = useState(initialState)

  const { doFireTx } = state

  const {
    amountDAI,
    allowUnlimitedApprove,
    web3Utils,
    contracts,
    network,
    walletAddress,
    walletProvider,
  } = context

  let { visualComponents, commonVisualProps, functionName, hatID } = stepDetails
  // All of these are required every time: Error, Aborted, Confirm
  // Todo: allow loading the default instead of forcing to provide them in DappySchema?
  const {
    visual: Confirming,
    props: confirmingProps,
  } = visualComponents.confirming
  const { visual: Error, props: errorProps } = visualComponents.error
  const { visual: Aborted, props: abortedProps } = visualComponents.aborted
  const { visual: Confirm, props: confirmProps } = visualComponents.confirm

  // Todo: Add as an option in dappi schema
  const onRetry = () => {
    setState(initialState)
    goToStep(1)
  }

  const getDisplayComponent = () => {
    let component
    switch (state.txStatus) {
      case "pending":
        component = <Confirming {...confirmingProps} {...commonVisualProps} />
        break
      case "error":
        component = (
          <Error onSubmit={onRetry} {...errorProps} {...commonVisualProps} />
        )
        break
      case "aborted":
        component = (
          <Aborted
            onSubmit={onRetry}
            {...abortedProps}
            {...commonVisualProps}
          />
        )
        break
      default:
        component = <Confirm {...confirmProps} {...commonVisualProps} />
    }
    return component
  }

  const handleFireResponse = async (res, skipConfirm) => {
    if (res.error) {
      console.log(res.error.message)
      if (res.error.txErrorType === "aborted") setState({ txStatus: "aborted" })
      else {
        setState({ txStatus: "error" })
      }
    } else if (res.approvalComplete) {
      // Only relevant for approvals
      nextStep()
    } else if (skipConfirm) {
      console.log("Tx submitted: " + res.tx.hash)
      nextStep()
    } else if (res.tx) {
      setState({ txStatus: "pending" })
      console.log("Tx submitted: " + res.tx.hash)
      //Todo add tx revert handling
      walletProvider
        .waitForTransaction(res.tx.hash, 1)
        .then(txReceipt => {
          if (txReceipt.status === 1) {
            nextStep()
            return
          }
          console.log("Tx was mined, but failed")
          // console.log(txReceipt)
          setState({ txStatus: "error" })
        })
        .catch(e => console.log(e)) // use for timeout?
    }
  }

  const approve = async () => {
    const { dai, rdai } = contracts
    setState({ doFireTx: false })
    // todo: add tx gas estimation failure
    const res = await web3Utils.approve(
      dai,
      walletAddress,
      rdai.address,
      amountDAI,
      allowUnlimitedApprove
    )
    handleFireResponse(res, true)
  }

  const mint = async () => {
    const { rdai } = contracts
    setState({ doFireTx: false })
    const res = await web3Utils.mintRDAI({
      rtokenContract: rdai,
      amount: amountDAI,
    })
    handleFireResponse(res)
  }

  const mintWithSelectedHat = async hatID => {
    const { rdai } = contracts
    const network = rdai.signer.provider.network.name
    setState({ doFireTx: false })
    // TODO: load recipients and proportions from context
    const res = await web3Utils.mintWithSelectedHatRDAI({
      rtokenContract: rdai,
      amount: amountDAI,
      hatID,
    })
    handleFireResponse(res)
  }

  const redeemAll = async () => {
    const { rdai } = contracts
    setState({ doFireTx: false })
    const res = await web3Utils.redeemAllRDAI({
      rtokenContract: rdai,
    })
    handleFireResponse(res)
  }

  const fire = () => {
    if (functionName === "approve") approve()
    else if (functionName === "mint") mint()
    else if (functionName === "redeemAll") redeemAll()
    else if (functionName === "mintWithSelectedHat") mintWithSelectedHat(hatID)
    else console.log(`Error FireTx.js: No function found "${functionName}"`)
  }

  useEffect(() => {
    if (web3Utils && isActive && doFireTx) fire()
  }, [doFireTx, network, walletAddress, isActive])

  return <div>{getDisplayComponent()}</div>
}

export default FireTx
