import React, { useContext, useState, useEffect } from "react"
import { Context } from "../../context"

const ethers = require("ethers")

const { parseUnits } = ethers.utils

const LoadWallet = ({ nextStep, goToStep }) => {
  const [context, setContext] = useContext(Context)

  const initialState = {
    doLoadWallet: true,
    walletStatus: null,
  }
  const [state, setState] = useState(initialState)
  const { doLoadWallet, walletStatus } = state

  const {
    web3Utils,
    walletProvider,
    amountDAI,
    stepDetails,
    stepOrder,
  } = context

  const {
    visualComponents,
    commonVisualProps,
    contractsToLoad,
    allowancesRequested,
    pauseTimeMs = 1000,
    balancesRequested,
    requiredBalances,
    allowSwap,
  } = stepDetails.loadWallet

  const { visual: Loading, props: loadingProps } = visualComponents.loading
  let InsufficientBalance = <></>
  let insufficientBalanceProps = {}
  if (requiredBalances) {
    ;({
      visual: InsufficientBalance,
      props: insufficientBalanceProps,
    } = visualComponents.insufficientBalance)
  }
  const { visual: Error, props: errorProps } = visualComponents.error

  const onRetry = () => {
    setState(initialState)
    goToStep(1)
  }

  const currentStep = stepOrder.indexOf(stepDetails.id)
  const skipSwapStep = () => {
    goToStep(4)
  }
  const skipApprovalStep = () => {
    goToStep(6)
  }

  let display
  switch (walletStatus) {
    case "error":
      display = (
        <Error onSubmit={onRetry} {...commonVisualProps} {...errorProps} />
      )
      break
    case "insufficientBalance":
      display = (
        <InsufficientBalance
          onSubmit={onRetry}
          {...commonVisualProps}
          {...insufficientBalanceProps}
        />
      )
      break
    default:
      display = <Loading {...commonVisualProps} {...loadingProps} />
  }

  const loadWallet = async () => {
    setState({
      ...state,
      doLoadWallet: false,
    })

    const {
      contracts,
      allowances,
      error,
      balances,
    } = await web3Utils.loadContracts({
      walletProvider,
      contractNames: contractsToLoad,
      allowancesRequested,
      balancesRequested,
    })
    if (error) {
      console.log(error.message)
      setState({ ...state, doLoadWallet: false, walletStatus: "error" })
      return
    }
    setContext({
      ...context,
      contracts,
      allowances,
      balances,
    })

    // Check if user has adequate balance
    const insufficientBalances = web3Utils.checkRequiredBalance({
      requiredBalances,
      balances,
    })
    if (Object.keys(insufficientBalances).length > 0) {
      if (allowSwap) {
        nextStep()
        return
      }
      setState({
        ...state,
        doLoadWallet: false,
        walletStatus: "insufficientBalance",
      })
      return
    }

    if (amountDAI && allowances && allowances.dai) {
      const amountDAI_BN = parseUnits(amountDAI.toString(), 18)
      if (amountDAI_BN.lte(allowances.dai.rdai)) {
        skipApprovalStep()
        return
      }
    }
    skipSwapStep()
  }

  useEffect(() => {
    if (web3Utils && doLoadWallet) loadWallet()
  }, [doLoadWallet])

  return <>{display}</>
}

export default LoadWallet
