import React, { useContext, useState, useEffect } from 'react';
import { Context } from '../../context';

const ChooseWallet = ({ nextStep, goToStep }) => {
  const [context, setContext] = useContext(Context);
  const { stepDetails, web3Utils, infuraKey } = context;

  if (stepDetails && stepDetails.chooseWallet) {
    const { visualComponents } = stepDetails.chooseWallet;
    const { visual: WalletPromptView } = visualComponents.chooseWallet;

    const onSubmit = amount => {
      nextStep();
    };
    const onSelectWallet = async name => {
      if (name === 'Coinbase') {
        const {
          hasWallet,
          isUnlocked,
          walletAddress,
          walletProvider,
          network,
          error
        } = await web3Utils.unlockCoinbaseWallet(infuraKey);
        if (error) console.log(error.error.message);
        setContext({
          ...context,
          hasWallet,
          isUnlocked,
          walletAddress,
          walletProvider,
          network,
          error: ''
        });
        goToStep(4);
      }
    };

    return (
      <>
        <WalletPromptView
          suggestedWallet="Metamask"
          alternateWallets={['Coinbase']}
          onSelectWallet={onSelectWallet}
          onContinue={nextStep}
        />
      </>
    );
  }
  return <></>;
};

export default ChooseWallet;
