// styleguide/ThemeWrapper.js
import React, { Component } from 'react';
import { ThemeProvider } from 'styled-components';
import PropTypes from 'prop-types';

import main from './themes/main';
import dark from './themes/dark';

/**
 * Wrap your app with this component to change the Feather theme.
 */

export default class FeatherWrapper extends Component {
  getTheme(name) {
    if (name === 'dark') return dark;
    // Add a new theme here
    return main;
  }
  render() {
    return (
      <ThemeProvider theme={this.getTheme(this.props.themeName)}>
        {this.props.children}
      </ThemeProvider>
    );
  }
}

FeatherWrapper.propTypes = {
  /** The desired theme. Optional, defaults to main. */
  themeName: PropTypes.string
  // children: PropTypes.any()
};
FeatherWrapper.defaultProps = {
  /** The desired theme. Optional, defaults to main. */
  themeName: 'main'
  // children: PropTypes.any()
};
