import React, { useContext, useState, useEffect } from 'react';
import { Context } from '../../context';

const Swap0x = ({ nextStep, goToStep }) => {
  const [context, setContext] = useContext(Context);
  const {
    stepOrder,
    stepDetails,
    infuraKey,
    web3Utils,
    walletProvider
  } = context;

  const initialState = {
    doGetOrder: true,
    orderStatus: '',
    sellAmount: null,
    protocolFee: null,
    order: null
  };
  const [state, setState] = useState(initialState);
  const { doGetOrder, orderStatus, sellAmount, protocolFee, order } = state;

  const buyToken = 'DAI';
  const buyAmount = '10000000000000000000';
  const sellToken = 'ETH';

  const get0xOrder = async () => {
    setState({
      ...state,
      doGetOrder: false
    });
    const { order: _order, error } = await web3Utils.get0xOrder({
      buyToken,
      buyAmount,
      sellToken
    });
    if (error) {
      console.log(error.message);
      setState({ ...state, doGetOrder: false, orderStatus: 'error' });
      return;
    }
    const { sellAmount: _sellAmount, protocolFee: _protocolFee } = _order;

    setState({
      ...state,
      sellAmount: _sellAmount,
      protocolFee: _protocolFee,
      order: _order,
      doGetOrder: false
    });
  };

  const onPlaceOrder = async name => {
    const { tx, error } = await web3Utils.send0xOrder({
      order,
      walletProvider
    });
    if (error) {
      console.log(error.error.message);
      setState({ ...state, orderStatus: 'error' });
      return;
    }
  };

  useEffect(() => {
    if (web3Utils && doGetOrder) get0xOrder();
  }, [doGetOrder]);

  if (stepDetails && stepDetails.swap0x) {
    const { visualComponents } = stepDetails.swap0x;
    const { visual: Swap0xView } = visualComponents.swap0x;
    return (
      <Swap0xView
        buyToken={buyToken}
        sellToken={sellToken}
        buyAmount={buyAmount}
        sellAmount={sellAmount}
        fee={protocolFee}
        onPlaceOrder={onPlaceOrder}
      />
    );
  }
  return <></>;
};

export default Swap0x;
